export default {
	data() {
		return {
			imgList: '',
			current: 0,
			winHeight: 0,
			smalHeight: 0,
			is_authentication: ''
		};
	},
	components: {},
	computed: {},
	methods: {
		chooseImg(e) {
			this.current = e
		},
		link() {
			this.$store.dispatch('getIsAuto', {}).then(res => {
				uni.navigateTo({
					url: `/pages/extension/share-detail?id=${this.imgList[this.current].id}`
				})
			}).catch(err => {
				uni.showToast({
					title: ' ',
					icon: 'none',
					duration: 0
				})
				uni.showModal({
					title: '提示',
					content: '请先完成实名认证',
					success: (res) => {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/mine/index'
							})
						}
					}
				})
			})
		}
	},
	onLoad() {
		uni.getSystemInfo({
			success: (res) => {
				this.winHeight = res.windowHeight
			}
		});
		this.$store.dispatch("getShareImgList", {}).then(res => {
			this.imgList = res.data;
		}).catch(err => {});
		this.$nextTick(() => {
			uni.createSelectorQuery().select('.small-img').fields({
				size: true
			}, data => {
				this.smalHeight = data.height
			}).exec()
		})
	},
	onNavigationBarButtonTap(index) {
		this.link()
	}
}
